<template>
  <div class="gcal_">
    <div>
      <div
        class="title"
        style="text-align: center; font-size: 20px; font-weight: 700"
      >
        {{ info.btn_title }}
      </div>
      <div class="time" style="text-align: center">
        发布时间：{{ info.time }}
      </div>
      <div class="js">{{ info.js }}</div>
      <div class="img">
        <img style="width: 350px; height: 200px" :src="info.img" alt="" />
      </div>

      <div class="box2">
        <div class="images" v-for="item in images" :key="item.index">
          <img style="width: 100px; height: 100px" :src="item" alt="" />
        </div>
      </div>

      <div class="cont">{{ info.cont }}</div>
      <div class="btn">
        <div class="left"  @click="left()"><el-button :disabled="this.$route.query.id==1?true:false">上一页</el-button></div>
        <div class="right" @click="right()"><el-button>下一页</el-button></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "gcal_",
  data() {
    return {
      info: [
        // {
        //   id: 1,
        //   time:'',
        //   img: require(),
        //   title: "",
        // js:'',
        // cont:''
        // },
      ],
      images: [],
    };
  },
  methods: {
    left() {
      
    },
    right() {
      
    },
    getinfo() {
      this.$axios
        .get("/get_gcal_id", {
          //127.0.0.1是本地服务器     3001是端口号
          params: {
            id: this.$route.query.id,
            bstypecategory_id: this.$route.query.bstypecategory_id,
          },
        })
        // 在后台查询信息 返回res结果
        .then((res) => {
          if (res.data.code != 200) {
            // 提示信息
            return this.$toast({
              // toast是vant组件（要安装和引入才能提示）
              message: "获取工程案例对应id信息失败",
              icon: "fail",
            });
          }
          // console.log(res.data.data); //  打印后台传给前台的数据
          // 查询成功,把结果赋给icon_arr
          this.info = res.data.data[0];
          // console.log(this.info.images);
          this.images = this.info.images.split(",");
          // console.log(this.info.images);
        })
        .catch((error) => {
          //报错
        });
    },
  },
  mounted() {
    this.getinfo();
    // console.log(this.$route.query.id);
    // console.log(this.$route.query.bstypecategory_id);
  },
};
</script>

<style lang="scss" scoped>
.gcal_ {
  margin-top: 115px;
  display: flex;
  justify-content: center;
  align-items: center;

  .box2 {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  .btn {
    display: flex;
    justify-content: space-between;
  }
}
</style>